.dashboard-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin: 24px;
}

.dashboard-header .div1 {
  grid-area: 1 / 1 / 4 / 2;
  display: flex;
  justify-content: left;
  align-items: center;
}

.dashboard-header .div2 {
  grid-area: 1 / 2 / 2 / 3;
  text-align: end;
}

.dashboard-header .div3 {
  grid-area: 2 / 2 / 3 / 3;
  text-align: end;
}

.dashboard-header .div4 {
  grid-area: 3 / 2 / 4 / 3;
  text-align: end;
}

.dashboard-header-name {
  display: grid;
  grid-template-columns: 1.75fr 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  direction: row;
  justify-content: left;
  align-items: center;
}

.dashboard-header-name .div1 {
  grid-area: 1 / 1 / 2 / 2;
}

.dashboard-header-name .div2 {
  grid-area: 1 / 2 / 2 / 3;
}

.dashboard-header-name .div3 {
  grid-area: 1 / 3 / 2 / 4;
}

.dashboard-header-name .div4 {
  grid-area: 1 / 4 / 2 / 5;
}